.profile-page{
  padding-top: 1em;
  flex-grow: 1;
  background-image: url(../img/figure2.svg);
  background-repeat: no-repeat;
  background-position: right bottom;
  background-size: 66.1875em auto;
}

.profile{
  display: grid;
  grid-template-columns: 19.5625em calc(100% - 21.5625em - 12.875em) 12.875em;
  gap: 1em;
}

.bridged,
.level{
  padding: 1em;
  border-radius: 0.75em;
  border: 1px solid rgba(205, 205, 205, 0.50);
  background: #F1F1FF;
  &__top{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }
}

.bridged{
  &__nums{
    margin-top: .5em;
    display: flex;
    align-items: center;
  }
  .currency{
    @include lastM(0 .5em 0 0);
  }
  &__btn{
    padding: 0 .72em;
    width: 7.25em;
    height: 2em;
    border-radius: 0.25em;
    img{
      width: .75em;
      margin-left: .625em;
    }
    span{
      font-size: .75em;
    }
  }
  &__bottom{
    margin-top: .25em;
  }
}

.currency{
  display: flex;
  align-items: center;
  font-weight: 500;
  img{
    width: 1em;
    margin-left: .125em;
  }
}

.level{
  grid-column: 2/4;
  &__top{
    margin-bottom: .31em;
  }
  &__badge{
    padding: .25em .5em;
    font-weight: 500;
    border-radius: 0.375em;
    background: rgba(67, 78, 220, 0.15);
    span{
      color: $primary;
    }
  }
  &__text{
    margin-top: .31em;
  }
}

.progress-bar{
  border-radius: 6.25em;
  background: #FFF;
  height: 1.125em;
  position: relative;
  span{
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    background-color: $primary;
    border-radius: inherit;
    background-image: url(../img/line5.svg);
    background-repeat: repeat-x;
    background-size: auto 100%;
  }
}

.tweet{
  background: #FFF;
  .bridged__btn{
    img{
      width: 1em;
    }
  }
  .bridged__bottom{
    margin-top: 0;
    @include grid100gap(.25em);
  }
}

.spins{
  background: #FFF;
  .bridged__bottom{
    max-width: 11.3125em;
    margin-top: .19em;
  }
}

.rank{
  padding: 1em;
  border-radius: 0.75em;
  border: 1px solid rgba(205, 205, 205, 0.50);
  background: #FFF;
  &__info{
    @include grid100gap(.25em);
  }
}

.faq{
  margin-top: 2em;
  &__title{
    font-size: 1.25em;
    line-height: 125%;
    font-family: $font2;
    font-weight: 600;
  }
  &__items{
    margin-top: 1em;
    @include grid100gap(.5em);
  }
}

.qa-card{
  padding: .94em 1.5em;
  border-radius: 0.75em;
  border: 1px solid rgba(205, 205, 205, 0.50);
  background: $white;
  box-shadow: $shadow;
  &._active{
    .qa-card__btn svg{
      transform: rotate(-180deg);
    }
  }
  &__show{
    display: flex;
    align-items: center;
    justify-content: space-between;
    transition: all .3s;
    &:hover{
      cursor: pointer;
      color: $primary;
      path{
        stroke: $primary;
      }
    }
  }
  &__btn{
    width: 1.5em;
    margin-left: 1em;
    flex-shrink: 0;
    @include center;
    svg{
      width: 100%;
    }
  }
  &__hidden{
    margin-top: .875em;
    display: none;
  }
  &__text{
    a{
      color: $primary;
      text-decoration: underline;
    }
  }
}


@media screen and (max-width: 1024px){
  .profile-page{
    background-image: unset;
  }

  .profile{
    grid-template-columns: 13.25em calc(100% - 24.375em) 9.125em;
  }

  .bridged{
    @media screen and (min-width: 577px){
      padding-bottom: 3.25em;
      position: relative;
      &__btn{
        position: absolute;
        bottom: .75em;
        left: 1em;
        width: calc(100% - 2em);
      }
    }
  }

  .level{
    @media screen and (min-width: 577px){
      &__top{
        @include grid100gap(.5em);
      }
    }
  }

  .tweet{
    @media screen and (min-width: 577px){
      .bridged__bottom{
        margin-top: .5em;
      }
    }
  }

  .spins{
    @media screen and (min-width: 577px){
      .bridged__bottom{
        margin-top: .72em;
      }
    }
  }

  .rank{
    @media screen and (min-width: 577px){
      .text12{
        font-size: .625em;
      }
    }
  }

  .faq{
    margin-top: 1em;
  }
}


@media screen and (max-width: 576px){
  .profile{
    grid-template-columns: 100%;
    gap: .25em;
  }

  .rank,
  .bridged,
  .level{
    padding: .75em 1em;
  }

  .level{
    grid-column: 1/-1;
  }

  .faq{
    margin-top: .75em;
    &__items{
      margin-top: .75em;
      gap: .375em;
    }
  }

  .qa-card{
    padding: .5em 1em;
    &__hidden{
      margin-top: .78em;
    }
    .h4{
      font-size: .75em;
    }
  }
}
