.access-airdrop{
  @include grid100gap(1.25em);
  text-align: center;
  &__nav{
    display: flex;
    justify-content: center;
    .m-btn{
      padding: 0 1em;
      span{
        font-size: 1.25em;
      }
    }
  }
}

.pin-code{
  padding: 0;
  margin: 0 auto;
  display: flex;
  justify-content:center;
  input {
    font-size: 1.25em;
    text-align: center;
    width: 2.5em;
    height: 3.2em;
    border-radius: 0.4em;
    border: 1px solid rgba(205, 205, 205, 0.50);
    background: #F8F8FF;
    box-shadow: 2px 2px 0px 0px #CCCCF8;
    margin-right: .2em;
    &:focus {
      border-color: $primary;
      outline: 1px solid $primary !important;
    }
    @media screen and (min-width: 577px){
      font-size: 1.5em;
    }
  }
}


@media screen and (max-width: 1024px){
  .access-airdrop{
    gap: .75em;
    .pin-code{
      padding: 1.5em 0;
    }
  }
}


@media screen and (max-width: 576px){
  .access-airdrop{
    gap: 1.75em;
    .pin-code{
      padding: 0;
    }
    &__nav{
      .m-btn{
        width: 100%;
        span{
          font-size: 1em;
        }
      }
    }
  }
}
