.z-index2{
  position: relative;
  z-index: 2;
}

.z-index3{
  position: relative;
  z-index: 3;
}

.about-main{
  margin-top: -1px;
  img{
    width: 100%;
    display: block;
  }
}

._round{
  position: absolute;
  width: 55.0625em;
  height: 55.0625em;
  opacity: 0.3;
  background: conic-gradient(from 180deg at 50% 50%, #4B55D5 0deg, #A6FBF3 208.125deg, #CDAEFD 360deg);
  filter: blur(200px);
  border-radius: 50%;
  pointer-events: none;
}

.build{
  padding: 3.625em 0;
  ._round{
    top: -25em;
    left: 50%;
    transform: translateX(-50%);
    z-index: -1;
  }
  &__inner{
    width: 42.375em;
    max-width: 100%;
    margin: 0 auto;
    @include grid100gap(1.5em);
    text-align: center;
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: .5em;
  }
}

.m-link{
  min-width: 24.3125em;
}

.about{
  clip-path: polygon( 0% 0%,0.667% 0.227%,0.667% 0.227%,10.365% 3.167%,20.224% 5.449%,30.202% 7.072%,40.26% 8.036%,50.357% 8.342%,60.452% 7.99%,70.504% 6.98%,80.473% 5.311%,90.319% 2.985%,100% 0%,100% 100%,100% 100%,90.754% 96.377%,81.262% 93.527%,71.582% 91.449%,61.773% 90.144%,51.892% 89.614%,41.997% 89.859%,32.146% 90.88%,22.397% 92.678%,12.808% 95.254%,3.437% 98.608%,0% 100%,0% 0% );
  padding: 19.25em 0 13.75em;
  margin-top: -7.25em;
  background-color: $primary;
  color: $white;
  position: relative;
  background-image: url(../img/rects.svg);
  background-size: 100% auto;
  background-position: left bottom -2em;
  background-repeat: no-repeat;
  .container{
    position: relative;
    z-index: 3;
  }
  &__rect,
  &__rect2{
    position: absolute;
    pointer-events: none;
  }
  &__rect{
    bottom: 11.87em;
    left: 0;
    width: 32.375em;
  }
  &__rect2{
    right: 44.48em;
    bottom: 10.35em;
    width: 15.125em;
  }
}

.advantages{
  &__top{
    max-width: 49em;
    margin: 0 auto;
    text-align: center;
  }
  &__grid{
    margin-top: 2em;
  }
}

.m-card{
  border-radius: 1em;
  border: 1px solid #CDCDCD;
  background: rgba(255, 255, 255, 0.20);
  backdrop-filter: blur(5px);
}

.m-card-bg{
  position: relative;
  background: rgba(255, 255, 255, 0.30);
  &:before{
    transition: all .3s;
    content: '';
    position: absolute;
    border-radius: inherit;
    opacity: 0.3;
    top: 0;
    left: 0;
    transform: rotate(3deg);
    z-index: -1;
    width: 100%;
    height: 100%;
  }
  &:hover{
    &:before{
      transform: rotate(0);
    }
  }
}

.advantage-card{
  padding: 1.5em;
  &__icon{
    height: 3.3125em;
    display: block;
  }
  &__title,
  &__desc{
    margin-top: 1.25em;
  }
  .h4{
    min-height: 2.5em;
    @include truncate(2);
  }
}

.about-info{
  margin-top: 5.94em;
  display: grid;
  grid-template-columns: 35.4375em calc(100% - 35.4375em - 4.75em);
  gap: 4.75em;
  align-items: center;
  padding-left: 3.31em;
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__items{
    @include grid100gap(1.19em);
  }
  &__item{
    padding: 2em;
    border-style: dashed;
  }
}

.how-work{
  padding: 1.3em 0;
  position: relative;
  &__star{
    position: absolute;
    top: -1.3em;
    right: 50%;
    width: 3em;
    transform: translateX(350%);
  }
  &__top{
    text-align: center;
  }
  &__grid{
    margin-top: 2.75em;
  }
  .advantage-card{
    &:before{
      background: #FD91C9;
    }
    .h4{
      min-height: unset;
    }
    &._blue:before{
      background: #444FDB;
    }
    &._blue2:before{
      background: #AE9BF8;
    }
  }
}

.join{
  padding: 5em 0;
  color: #fff;
  &__inner{
    border-radius: 1.5em;
    background-color: #434EDC;
    background-image: url(../img/join-bg.svg);
    background-size: cover;
    background-position: center;
    padding: 3.25em 2.25em;
    display: grid;
    grid-template-columns: 32.4375em calc(100% - 32.4375em - 1.625em);
    gap: 1.625em;
    align-items: center;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    max-width: 42.5em;
  }
  &__text{
    @include grid100gap(1.125em);
  }
  &__nav{
    margin-top: 4em;
  }
  &__btn{
    svg{
      width: 1.5em;
      margin-right: .625em;
    }
    &:hover{
      color: $primary;
      background-color: #fff;
      path:not(._stroke){
        fill: $primary;
      }
      ._stroke{
        stroke: $primary;
      }
    }
  }
}



@media screen and (max-width: 1024px){
  .about-main{
    height: 35.25em;
    img{
      height: 100%;
      object-fit: cover;
    }
  }

  .build{
    padding: 5.56em 0;
    ._round{
      width: 72.9375em;
      height: 72.9375em;
      right: 3.19em;
      left: unset;
      transform: unset;
      top: -34em;
    }
    &__inner{
      gap: 1.75em;
    }
    &__nav{
      margin-top: 0;
    }
  }

  .about{
    z-index: 3;
    padding: 14em 0 20em;
    margin-top: 0;
    clip-path: polygon( -20.313% 0%,-19.374% 0.227%,-19.374% 0.227%,-5.736% 3.167%,8.127% 5.449%,22.16% 7.072%,36.304% 8.036%,50.502% 8.342%,64.698% 7.99%,78.834% 6.98%,92.853% 5.311%,106.698% 2.985%,120.313% 0%,120.313% 100%,120.313% 100%,107.31% 96.377%,93.962% 93.527%,80.35% 91.449%,66.555% 90.144%,52.66% 89.614%,38.746% 89.859%,24.893% 90.88%,11.183% 92.678%,-2.301% 95.254%,-15.479% 98.608%,-20.313% 100%,-20.313% 0% );
    background-image: unset;
    &__rect2{
      right: 19.29em;
    }
  }

  .advantage-card{
    padding: 1.25em 1em;
    &__title,
    &__desc{
      margin-top: .75em;
    }
  }

  .about-info{
    margin-top: 7em;
    padding: 0;
    grid-template-columns: 29.0625em calc(100% - 31.25em);
    gap: 2.0625em;
  }

  .how-work{
    padding: 1.56em 0;
    &__star{
      top: -.5em;
    }
    &__grid{
      margin-top: 2.19em;
    }
    .advantage-card{
      &:before{
        transform: rotate(-2deg);
      }
      &__icon{
        width: 2.75em;
        height: auto;
      }
    }
  }

  .join{
    padding: 6.25em 0;
    &__inner{
      grid-template-columns: 25.625em calc(100% - 26.125em);
      gap: .5em;
      padding: 3.75em .625em;
    }
    &__content{
      max-width: 32em;
    }
  }
}


@media screen and (max-width: 576px){
  .about-main{
    height: 27.5em;
  }

  .build{
    padding: 1.8em 0 5em;
    overflow: hidden;
    ._round{
      right: -19.38em;
      top: -28em;
    }
  }

  .m-link{
    min-width: unset;
    width: 100%;
  }

  .about{
    clip-path: polygon( -0% -0%,3.109% 0.323%,3.109% 0.323%,12.358% 1.164%,21.939% 1.81%,31.765% 2.262%,41.748% 2.52%,51.799% 2.583%,61.83% 2.453%,71.754% 2.129%,81.482% 1.612%,90.927% 0.902%,100% -0%,100% 100%,100% 100%,91.036% 99.006%,81.621% 98.224%,71.863% 97.656%,61.867% 97.3%,51.743% 97.157%,41.595% 97.229%,31.532% 97.515%,21.661% 98.015%,12.087% 98.73%,2.919% 99.66%,-0% 100%,-0% -0% );
    margin-top: -4em;
    padding: 9em 0 10em;
    z-index: 2;
    overflow: hidden;
    &__rect{
      left: -1.19em;
      bottom: 23.75em;
    }
    &__rect2{
      display: none;
    }
  }

  .advantages{
    &__grid{
      margin-top: 1.25em;
    }
  }

  .advantage-card{
    &__top{
      display: flex;
      align-items: center;
    }
    &__icon{
      height: auto;
      width: 2.75em;
      margin-right: .75em;
    }
    &__title{
      margin: 0;
      max-width: 13em;
    }
    .h4{
      min-height: unset;
    }
  }

  .about-info{
    margin-top: 8.88em;
    grid-template-columns: 100%;
    gap: 1.625em;
    &__img{
      @include fullMob;
    }
    &__items{
      gap: .75em;
    }
    &__item{
      padding: 1.25em 1em;
    }
  }

  .how-work{
    padding: 4.38em 0;
    &__star{
      top: 2.81em;
      width: 1.9375em;
      transform: translateX(440%);
    }
    &__grid{
      margin-top: 1.875em;
    }
    .advantage-card{
      &:before{
        transform: rotate(-1deg);
      }
    }
  }

  .join{
    &__inner{
      grid-template-columns: 100%;
      padding: 2.5em 1em 13em;
      gap: 0;
      position: relative;
    }
    &__content{
      order: -1;
    }
    &__nav{
      position: absolute;
      bottom: 1.875em;
      left: 0;
      width: 100%;
      padding: 0 1em;
    }
  }
}
