.operation,
.point{
  flex-grow: 1;
  background: $bg;
  position: relative;
  overflow: hidden;
  &:before{
    content: '';
    position: absolute;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background-image: url(../img/figure2.svg);
    background-repeat: no-repeat;
    background-position: right bottom;
    background-size: 66.1875em auto;
    pointer-events: none;
  }
}

.point{
  padding: 5.625em 0;
  min-height: calc(100 * var(--vh) - 4.25em);
  &__content{
    max-width: 35.375em;
    width: 100%;
    position: relative;
    z-index: 2;
  }
  .h1{
    br{
      display: none;
    }
    &._big{
      @media screen and (min-width: 1025px){
        width: calc(100% + 1em);
      }
    }
  }
  &__items{
    @include grid100gap(.625em);
    margin-top: 2.25em;
  }
  &__item{
    background-color: rgba(255, 255, 255, 0.60);
    border: $border;
    padding: 1.13em 1.5em;
    border-radius: .75em;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      width: .4375em;
      height: calc(100% + 2px);
      left: -1px;
      top: -1px;
      background-color: $primary;
      border-radius: 1em 0 0 1em;
    }
  }
  &__bottom{
    display: flex;
    align-items: center;
    margin-top: 3em;
  }
  &__btn{
    width: 17.0625em;
    margin-right: 2.31em;
    flex-shrink: 0;
  }
  &__link{
    &:hover{
      color: $black;
    }
  }
  &__time,
  &__link{
    font-size: 1em;
    line-height: 119%;
    font-weight: 600;
    color: $primary;
  }
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-right: 9em;
  }
  &__sidebar{
    width: 26.0625em;
    flex-shrink: 0;
  }
  &__hint{
    margin-top: 1.5em;
  }
  .message{
    margin-top: 1.5em;
  }
}

.wheel{
  right: -2.54em;
  bottom: -7.25em;
  position: absolute;
  width: 24.625em;
  animation-name: rotate-circle;
  animation-duration: 20s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  img{
    width: 100%;
    display: block;
  }
}

@keyframes rotate-circle {
  to {
    transform: rotate(1turn);
  }
}

.message{
  display: flex;
  align-items: center;
  padding: .75em 1em;
  border-radius: 0.5em;
  background: rgba(67, 78, 220, 0.20);
  font-weight: 500;
  color: $primary;
  img{
    width: 1.75em;
    margin-right: .625em;
  }
}


@media screen and (max-width: 1024px){
  .wheel{
    //width: 18.375em;
    right: -6.5em;
    bottom: -8.25em;
  }

  .point{
    padding: 5.625em 0 13em;
    &__content{
      margin: 0 auto;
    }
    .h4,.h1{
      text-align: center;
    }
    &__bottom{
      justify-content: center;
    }
    &__inner{
      flex-direction: column;
      padding-right: 0;
    }
    &__sidebar{
      width: 32.0625em;
      max-width: 100%;
      margin: 5.88em 0 0;
    }
    .action{
      @media screen and (min-width: 577px){
        font-size: 1.25em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .point{
    min-height: calc(100 * var(--vh) - 3.5em);
    .h1{
      br{
        display: unset;
      }
    }
    &__items{
      margin-top: 1.75em;
      gap: .375em;
    }
    &__item{
      padding: 1em 1.41em;
    }
    &__bottom{
      margin-top: 1.75em;
      flex-direction: column;
    }
    &__btn{
      width: 100%;
      margin: 0 0 1.75em;
    }
    .message{
      margin-top: 1.75em;
    }
  }

  .message{
    padding: .625em 1em;
  }
}
