.advantage-card2{
  @media screen and (min-width: 577px){
    position: relative;
    padding-left: 6.06em;
    .advantage-card{
      &__icon{
        position: absolute;
        left: 1.5em;
        top: 50%;
        transform: translateY(-50%);
        width: 3.3125em;
        height: auto;
      }
      &__title{
        margin: 0;
      }
      &__desc{
        margin-top: .75em;
      }
    }
    .h4{
      min-height: unset;
    }
  }
}

.about2{
  clip-path: polygon( -20.313% 15.786%,-20.313% 15.786%,-6.695% 10.191%,7.108% 5.823%,21.049% 2.682%,35.083% 0.768%,49.167% 0.083%,63.254% 0.625%,77.299% 2.395%,91.258% 5.394%,105.084% 9.622%,118.733% 15.078%,120.313% 15.786%,120.313% 87.05%,120.313% 87.05%,107.245% 91.479%,94.06% 94.984%,80.785% 97.564%,67.445% 99.218%,54.067% 99.947%,40.68% 99.749%,27.31% 98.624%,13.983% 96.571%,0.726% 93.59%,-12.433% 89.681%,-20.313% 87.05%,-20.313% 15.786% );
  background-color: $primary;
  color: #fff;
  padding: 12em 0;
  position: relative;
  overflow: hidden;
  ._round{
    filter: blur(250px);
    background: $gradient;
    width: 72.9375rem;
    height: 72.9375rem;
    top: -35em;
    left: -14em;
  }
}

.deploy{
  padding: 12.5em 0;
  &__inner{
    margin: 0 auto;
    width: 42.5em;
    max-width: 100%;
  }
  &__text{
    @include grid100gap(1.25em);
    text-align: center;
  }
  &__nav{
    margin-top: 2.19em;
    display: grid;
    gap: 1em;
    grid-template-columns: repeat(3,calc(100% / 3 - 2em / 3));
  }
}

.editor{
  margin-top: 2.19em;
  img{
    width: 100%;
    display: block;
  }
}

.stat{
  padding: 5em 0;
  background-color: $primary;
  color: #fff;
  &__text{
    text-align: center;
    @include grid100gap(1.25em);
  }
}

.stat-table{
  margin-top: 2.75em;
  table{
    min-width: 100%;
    border-spacing: 1.06em 0;
  }
  th,
  td{
    text-align: center;
    &:nth-child(1){
      text-align: left;
      width: 20.75em;
    }
    &:nth-child(2){
      width: 20.6875em;
    }
    &:nth-child(3),
    &:nth-child(4),
    &:nth-child(5){
      width: 13.5em;
    }
  }
  th{
    padding-bottom: .69em;
    text-transform: uppercase;
  }
  td{
    height: 3.25em;
    border-bottom: 1px dashed #CDCDCD;
    &:nth-child(2){
      background: rgba(255, 255, 255, 0.20);
      backdrop-filter: blur(5px);
      border-bottom-color: $primary;
    }
  }
  tr{
    td:nth-child(2){
      border-left: 2px solid #FFF;
      border-right: 2px solid #FFF;
    }
    &:nth-child(1){
      td:nth-child(2){
        border-top: 2px solid #FFF;
        border-top-left-radius: 1em;
        border-top-right-radius: 1em;
      }
    }
    &:last-child{
      td{
        border-bottom: none;
      }
      td:nth-child(2){
        border-bottom: 2px solid #FFF !important;
        border-bottom-left-radius: 1em;
        border-bottom-right-radius: 1em;
      }
    }
    &:nth-child(3){
      td{
        &:nth-child(3){
          border-bottom: none;
        }
      }
    }
    &:nth-child(4),
    &:nth-child(5),
    &:nth-child(6),
    &:nth-child(7),
    &:nth-child(8){
      td:nth-child(3){
        border-left: 1px solid #CDCDCD;
        border-right: 1px solid #CDCDCD;
        background: rgba(255, 255, 255, 0.10);
        backdrop-filter: blur(5px);
      }
    }
    &:nth-child(8){
      td:nth-child(3),
      td:nth-child(4),
      td:nth-child(5){
        border-bottom-left-radius: 1em;
        border-bottom-right-radius: 1em;
        border-bottom: 1px solid #CDCDCD !important;
      }
    }
    &:nth-child(4){
      td:nth-child(3){
        border-top: 1px solid #CDCDCD;
        border-top-left-radius: 1em;
        border-top-right-radius: 1em;
      }
    }
    &:nth-child(5){
      td{
        &:nth-child(4){
          border-bottom: none;
        }
      }
    }
    &:nth-child(6){
      td:nth-child(4){
        border-top: 1px solid #CDCDCD;
        border-top-left-radius: 1em;
        border-top-right-radius: 1em;
      }
    }
    &:nth-child(6),
    &:nth-child(7),
    &:nth-child(8){
      td:nth-child(4){
        border-left: 1px solid #CDCDCD;
        border-right: 1px solid #CDCDCD;
        background: rgba(255, 255, 255, 0.10);
        backdrop-filter: blur(5px);
      }
    }
    &:nth-child(6){
      td{
        &:nth-child(5){
          border-bottom: none;
        }
      }
    }
    &:nth-child(7){
      td:nth-child(5){
        border-top: 1px solid #CDCDCD;
        border-top-left-radius: 1em;
        border-top-right-radius: 1em;
      }
    }
    &:nth-child(7),
    &:nth-child(8){
      td:nth-child(5){
        border-left: 1px solid #CDCDCD;
        border-right: 1px solid #CDCDCD;
        background: rgba(255, 255, 255, 0.10);
        backdrop-filter: blur(5px);
      }
    }
  }
  &__th{
    font-size: .75em;
    line-height: 120%;
  }
  &__th2{
    font-size: 1.25em;
    line-height: 120%;
    font-weight: 600;
  }
  &__title{
    font-size: 1em;
    line-height: 120%;
  }
  &__icon{
    width: 1.5em;
  }
}

.info{
  padding: 12.5em 0 5em;
  &__items{
    max-width: 71.5em;
    width: 100%;
    margin: 0 auto;
    @include grid100gap(1.625em);
  }
}

.info-card{
  display: flex;
  align-items: center;
  justify-content: space-between;
  &._blue{
    &:before{
      background: #E4DEFA;
    }
  }
  &._blue2{
    &:before{
      background: #444FDB;
    }
  }
  &._reverse{
    @media screen and (min-width: 577px){
      justify-content: unset;
      .info-card__img{
        order: -1;
        margin: 0 .75em 0 1.56em;
      }
    }
  }
  &:before{
    background: #FD91C9;
    transform: rotate(1deg);
  }
  &__content{
    padding: 2.75em;
    flex-grow: 1;
    max-width: 40em;
    @include grid100gap(1.25em);
  }
  &__img{
    width: 27em;
    margin-right: 2.75em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
}

.timeline{
  padding: 5em 0;
  &__text{
    width: 40em;
    margin: 0 auto;
    max-width: 100%;
    text-align: center;
    @include grid100gap(1.25em);
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 2em;
  }
  &__items{
    display: flex;
    margin: 1.5em auto 0;
    width: 71.5em;
    max-width: 100%;
  }
}

.timeline-card{
  padding: 1em 2em;
  width: 25.27em;
  height: 6.875em;
  position: relative;
  display: flex;
  align-items: center;
  z-index: 3;
  &:nth-child(2){
    z-index: 2;
    margin-left: -2.5em;
    padding-left: 3.81em;
    .timeline-card__bg{
      path{
        fill: #EFEFFF;
      }
    }
  }
  &:nth-child(3){
    z-index: 1;
    margin-left: -2.5em;
    padding-left: 3.81em;
    .timeline-card__bg{
      path{
        fill: #E0E0FF;
      }
    }
  }
  &__bg{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    &._view2,
    &._view3{
      display: none;
    }
  }
  &__icon{
    width: 3.3125em;
    margin-right: 1.875em;
    position: relative;
    z-index: 2;
  }
  &__content{
    position: relative;
    z-index: 2;
    @include grid100gap(.5em);
  }
  &__month{
    padding: 0 .5em;
    border-radius: 0.25em;
    background: $primary;
    display: inline-flex;
    align-items: center;
    height: 1.4375em;
    color: #fff;
    margin-right: auto;
    span{
      font-size: .75em;
    }
  }
}


@media screen and (max-width: 1024px){
  .advantage-card2 {
    @media screen and (min-width: 577px) {
      padding-left: 5.56em;
    }
  }

  .deploy{
    padding: 9.375em 0;
  }

  .stat-table{
    table{
      border-spacing: .5em 0;
    }
    th,
    td{
      &:nth-child(1){
        width: 13em;
      }
      &:nth-child(2){
        width: 17em;
      }
      &:nth-child(3),
      &:nth-child(4),
      &:nth-child(5){
        width: 9.75em;
      }
    }
  }

  .info-card{
    &._reverse{
      @media screen and (min-width: 577px){
        .info-card__content{
          padding-left: 0;
        }
        .info-card__img{
          margin: 0 .875em 0 1.39em;
        }
      }
    }
    &__img{
      width: 23.625em;
    }
    &__content{
      padding: 2.375em 0 2.375em 2.375em;
    }
  }

  .timeline{
    padding: 5em 0 3em;
    &__nav{
      margin-top: 1.75em;
    }
  }

  .timeline-card{
    width: 21.9em;
    padding: 1em 1.75em;
    &:nth-child(2),
    &:nth-child(3){
      margin-left: -1.8em;
      padding-left: 2.37em;
    }
    &__bg{
      &._view1{
        display: none;
      }
      &._view2{
        display: block;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .about2{
    padding: 9.25em 0;
    clip-path: polygon( 0% 2.57%,3.399% 2.243%,3.399% 2.243%,12.919% 1.429%,22.545% 0.803%,32.25% 0.366%,42.005% 0.117%,51.781% 0.056%,61.551% 0.183%,71.286% 0.498%,80.958% 1.001%,90.539% 1.691%,100% 2.57%,100% 96.589%,100% 96.589%,90.664% 97.755%,81.118% 98.671%,71.412% 99.337%,61.595% 99.754%,51.718% 99.921%,41.83% 99.838%,31.98% 99.505%,22.22% 98.92%,12.598% 98.086%,3.164% 97%,0% 96.589%,0% 2.57% );
  }

  .deploy{
    padding: 6.25em 0;
    &__nav{
      margin-top: 1.25em;
      grid-template-columns: 100%;
      gap: .5em;
    }
  }

  .editor{
    margin-top: 1.25em;
  }

  .stat{
    padding: 4.38em 0 3em;
  }

  .stat-table{
    margin-top: 1.875em;
    overflow-x: auto;
    @include fullMobScroll;
    table{
      width: 51.875em;
      border-spacing: .625em 0;
      padding-bottom: 1.38em;
    }
    th,
    td{
      &:nth-child(1),
      &:nth-child(2),
      &:nth-child(3),
      &:nth-child(4),
      &:nth-child(5){
        width: 9.875em;
      }
    }
    &__th2{
      font-size: 1em;
    }
  }

  .info{
    padding: 6.25em 0 3.25em;
    &__items{
      gap: 1.5em;
    }
  }

  .info-card{
    display: block;
    &__content{
      padding: 1.5em 1em .31em;
      width: 100%;
    }
    &__img{
      width: 100%;
      display: block;
      margin: 0;
    }
    .h3{
      padding-right: 2em;
    }
  }

  .timeline{
    padding: 3.25em 0 0;
    &__items{
      margin-top: 1.25em;
      width: 11.875em;
      display: block;
    }
  }

  .timeline-card{
    width: 100%;
    height: 16.27em;
    padding: 2.44em .5em;
    flex-direction: column;
    text-align: center;
    &:nth-child(2),
    &:nth-child(3){
      margin: -2.4em 0 0;
      padding: 3.06em .5em;
    }
    &__bg{
      &._view2{
        display: none;
      }
      &._view3{
        display: block;
      }
    }
    &__icon{
      margin: 0 0 1.5em;
    }
    &__month{
      margin-left: auto;
    }
  }
}
