.invite{
  padding: 2.75em 0;
  flex-grow: 1;
  background: $bg;
  &__top{
    text-align: center;
  }
  &__items{
    width: 42.5em;
    max-width: 100%;
    margin: 2.25em auto 0;
    @include grid100gap(.25em);
  }
}

.invite-card{
  position: relative;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-height: 5em;
    padding: 1.375em;
    border-radius: 0.75em;
    border: 1px solid rgba(0, 0, 0, 0.10);
    background: #FFF;
    position: relative;
    z-index: 2;
  }
  &__badge{
    clip-path: polygon( 100% 100%,100% 0%,22.207% 0%,22.207% 0%,21.396% 0.061%,20.594% 0.244%,19.801% 0.545%,19.022% 0.964%,18.258% 1.497%,17.512% 2.144%,16.788% 2.901%,16.087% 3.766%,15.413% 4.738%,14.767% 5.815%,3.244% 26.45%,3.244% 26.45%,2.654% 27.606%,2.118% 28.862%,1.638% 30.211%,1.215% 31.641%,0.852% 33.145%,0.551% 34.713%,0.313% 36.336%,0.14% 38.005%,0.035% 39.71%,0% 41.443%,0% 58.557%,0% 58.557%,0.035% 60.29%,0.14% 61.995%,0.313% 63.664%,0.551% 65.287%,0.852% 66.855%,1.215% 68.359%,1.638% 69.789%,2.118% 71.138%,2.654% 72.394%,3.244% 73.55%,14.767% 94.185%,14.767% 94.185%,15.413% 95.262%,16.087% 96.234%,16.788% 97.1%,17.512% 97.857%,18.258% 98.503%,19.022% 99.036%,19.801% 99.455%,20.594% 99.756%,21.396% 99.939%,22.207% 100%,100% 100% );
    position: absolute;
    right: 100%;
    top: 50%;
    transform: translateY(-50%);
    width: 8.5em;
    height: 3.875em;
    background-color: $primary;
    padding: 0 .625em;
    font-family: $font2;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    img{
      width: 1.5em;
      margin-right: .25em;
    }
    span{
      font-size: .875em;
      line-height: 120%;
    }
  }
  &__title{
    font-size: 1em;
    line-height: 125%;
    font-family: $font2;
    color: $gray;
  }
  &__icon{
    width: 2em;
    margin-left: .5em;
    flex-shrink: 0;
  }
  &__btn{
    padding: 0 1em;
    height: 2.25em;
    min-width: 9.875em;
    ._view2{
      display: none;
    }
  }
}


@media screen and (max-width: 1024px){
  .invite{
    padding: 12em 0;
  }
}


@media screen and (max-width: 576px){
  .invite{
    padding: 9em 0;
    &__items{
      margin-top: 1.75em;
    }
  }

  .invite-card{
    width: calc(100% - 2.38em);
    margin-left: auto;
    &__inner{
      min-height: 4em;
      padding: 1em;
    }
    &__badge{
      height: 2.9375em;
      width: 6.375em;
      justify-content: unset;
      right: unset;
      left: -2.38em;
      clip-path: polygon( 99.515% 100%,99.515% 0%,23.907% 0%,23.907% 0%,22.828% 0.082%,21.76% 0.326%,20.706% 0.73%,19.669% 1.291%,18.654% 2.005%,17.663% 2.87%,16.701% 3.884%,15.771% 5.042%,14.876% 6.344%,14.02% 7.784%,4.721% 24.599%,4.721% 24.599%,3.951% 26.123%,3.251% 27.778%,2.623% 29.551%,2.071% 31.432%,1.597% 33.407%,1.204% 35.465%,0.893% 37.595%,0.668% 39.784%,0.532% 42.02%,0.485% 44.293%,0.485% 55.707%,0.485% 55.707%,0.532% 57.98%,0.668% 60.216%,0.893% 62.405%,1.204% 64.535%,1.597% 66.593%,2.071% 68.568%,2.623% 70.449%,3.251% 72.222%,3.951% 73.877%,4.721% 75.401%,14.02% 92.216%,14.02% 92.216%,14.876% 93.656%,15.771% 94.957%,16.701% 96.116%,17.663% 97.13%,18.654% 97.995%,19.669% 98.709%,20.706% 99.27%,21.76% 99.673%,22.828% 99.918%,23.907% 100%,99.515% 100% );
      span{
        display: none;
      }
    }
    &__title{
      font-size: .75em;
      a{
        display: none;
      }
    }
    &__btn{
      min-width: 5.125em;
      height: 1.6875em;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
      span{
        font-size: .75em;
      }
    }
  }
}
