.header{
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 4.25em;
    position: relative;
  }
  &__logo{
    display: block;
    width: 7.875em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
    ._view2{
      display: none;
    }
    @media screen and (min-width: 1025px){
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
    }
  }
  &._index{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    padding: .625em 0;
    z-index: 20;
    .header__inner{
      min-height: 3em;
      border-radius: 0.75rem;
      border: 1px solid #CDCDCD;
      background: #FFF;
      box-shadow: .25em .25em 0 #D7DCF9;
      padding: .375em 1em;
    }
    .header__logo{
      left: 1em;
    }
  }
}

.burger{
  display: none;
}

.menu{
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  &:before{
    content: '';
    width: 7.875em;
  }
}

.nav{
  display: flex;
  &__link{
    font-weight: 300;
    text-transform: uppercase;
    @include lastM(0 2.75em 0 0);
    border-top: 1px solid transparent;
    border-bottom: 1px solid transparent;
    span{
      font-size: .9375em;
      line-height: 120%;
    }
    &._active{
      border-bottom-color: $primary;
      color: $primary;
      font-weight: 500;
    }
  }
}

.header-action{
  display: flex;
  align-items: center;
  &__btn{
    margin-left: .75em;
    padding: 0 .75em;
    height: 2.75em;
    flex-shrink: 0;
    span{
      font-size: .75em;
    }
  }
}

.lang{
  position: relative;
  &__show{
    width: 1.5em;
    &:hover{
      cursor: pointer;
      path{
        stroke: $primary;
      }
    }
    svg{
      width: 100%;
    }
  }
  &__dropdown{
    position: absolute;
    top: 100%;
    margin-top: 1.16em;
    left: 50%;
    transform: translateX(-50%);
    border-radius: 0.75em;
    background: #FFF;
    box-shadow: 0 .25em .625em rgba(0, 0, 0, 0.06);
    width: 14.375em;
    padding: 1em;
    z-index: 5;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    &:before{
      content: '';
      position: absolute;
      background-image: url(../img/triangle.svg);
      width: 1.9375em;
      height: 0.96875em;
      @include bgImgSize(100%);
      top: -.78em;
      left: 50%;
      transform: translateX(-50%);
    }
  }
  &__item{
    padding: .5em 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-weight: 600;
    font-family: $font2;
    text-transform: uppercase;
    color: $gray;
    border-bottom: 1px dashed #CDCDCD;
    &:after{
      content: '';
      width: 1em;
      height: 1em;
      flex-shrink: 0;
      background-image: url(../img/success2.svg);
      @include bgImgSize(100%);
      margin-left: .25em;
      transition: all .3s;
      opacity: 0;
    }
    span{
      font-size: .75em;
      line-height: 125%;
    }
    &:first-child{
      padding-top: 0;
    }
    &:last-child{
      padding-bottom: 0;
      border-bottom: none;
    }
    &._active{
      color: $black;
      pointer-events: none;
      &:after{
        opacity: 1;
      }
    }
  }
  &._open{
    .lang__dropdown{
      opacity: 1;
      pointer-events: unset;
    }
  }
}

.header-user{
  padding: .375em;
  border-radius: 0.75em;
  border: 1px solid $grayLight;
  background-color: $white;
  margin-left: .75em;
  display: flex;
  align-items: center;
  &__name{
    color: #949494;
    display: flex;
    align-items: center;
    border-radius: 0.5em;
    background: #EFEFEF;
    padding: .375em;
    margin-left: .625em;
  }
  .avatar{
    width: 1.25em;
    height: 1.25em;
    margin-right: .75em;
  }
  &__info{
    padding-left: .375em;
  }
  &__top{
    text-align: right;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    img{
      width: 1em;
      flex-shrink: 0;
      margin-left: .25em;
    }
  }
  &__bottom{
    display: flex;
    align-items: center;
  }
  &__item{
    color: $gray;
    @include lastM(0 .375em 0 0);
  }
}


@media screen and (max-width: 1024px){
  .header{
    &__logo{
      position: relative;
      z-index: 21;
    }
    &._open{
      .menu{
        transform: translateX(0%);
      }
    }
    &._index{
      .header__logo{
        left: 0;
      }
    }
  }

  .burger{
    width: 2.75em;
    @include center;
    position: relative;
    z-index: 21;
    img{
      width: 100%;
      display: block;
    }
  }

  .menu{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    overflow-y: auto;
    background-color: #fff;
    z-index: 20;
    flex-direction: column;
    justify-content: unset;
    padding: 5em 1em 3em;
    transition: all .4s;
    transform: translateX(-120%);
    &:before{
      display: none;
    }
  }

  .nav{
    flex-direction: column;
    align-items: center;
    &__link{
      @include lastM(0 0 1.5em);
      span{
        font-size: 1.125em;
      }
    }
  }

  .header-action{
    margin-top: auto;
    padding-top: 2em;
    flex-direction: column;
    &__btn{
      margin: 0;
    }
  }

  .lang{
    order: 2;
    margin-top: 1.5em;
    &__dropdown{
      top: unset;
      margin: 0 0 .5em;
      bottom: 100%;
      &:before{
        display: none;
      }
    }
  }
}



@media screen and (max-width: 576px){
  .header{
    &__inner{
      height: 3.5em;
    }
    &__logo{
      width: 2em;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }

  .header-action{
    width: 100%;
    &__btn{
      width: 100%;
    }
  }
}
