.grid2,
.grid3,
.grid4{
  display: grid;
  gap: 1em;
}

.grid2{
  grid-template-columns: repeat(2,calc(50% - .5em));
}

.grid3{
  grid-template-columns: repeat(3,calc(100% / 3 - 2em / 3));
}

.grid4{
  grid-template-columns: repeat(4,calc(25% - 3em / 4));
}


@media screen and (max-width: 1024px){
  .grid2,
  .grid2-tablet,
  .grid3,
  .grid4{
    gap: .75em;
  }

  .grid3{
    grid-template-columns: repeat(3,calc(100% / 3 - .5em));
  }

  .grid4{
    grid-template-columns: repeat(4,calc(25% - 2.25em / 4));
  }

  .grid2,
  .grid2-tablet{
    grid-template-columns: repeat(2,calc(50% - .375em));
  }
}


@media screen and (max-width: 576px){
  .grid2,
  .grid2-tablet,
  .grid3,
  .grid4{
    gap: .5em;
    grid-template-columns: 100%;
  }
}
