.main{
  min-height: $vh;
  position: relative;
  padding: 6em .75em;
  @include center;
  flex-direction: column;
  &__wrap{
    padding: 1.66em 3.2em;
    position: relative;
    width: 30.25em;
    max-width: 100%;
    z-index: 3;
    &:before{
      content: '';
      position: absolute;
      border-radius: 1em;
      border: 1px solid #CDCDCD;
      background: rgba(255, 255, 255, 0.50);
      box-shadow: 5px 4px 4px 0px rgba(68, 68, 68, 0.20);
      backdrop-filter: blur(5px);
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
  &__inner{
    position: relative;
    z-index: 2;
    @include grid100gap(1.25em);
    text-align: center;
  }
  &__bottom{
    display: flex;
    justify-content: center;
    position: relative;
    z-index: 3;
  }
  &__item{
    @include lastM(0 .25em 0 0);
    width: 13.875em;
    height: 2.3125em;
    clip-path: polygon( 0% 0%,100% 0%,100% 25%,92.609% 86.531%,92.609% 86.531%,92.286% 88.963%,91.93% 91.179%,91.542% 93.17%,91.127% 94.925%,90.687% 96.437%,90.225% 97.695%,89.745% 98.689%,89.25% 99.411%,88.741% 99.851%,88.224% 100%,11.776% 100%,11.776% 100%,11.259% 99.851%,10.75% 99.411%,10.255% 98.689%,9.775% 97.695%,9.313% 96.437%,8.873% 94.925%,8.458% 93.17%,8.07% 91.179%,7.714% 88.963%,7.391% 86.531%,0% 25%,0% 0% );
    background-color: #FD91C9;
    @include center;
    color: #fff;
    font-family: $font2;
    font-weight: 600;
    span{
      font-size: .75em;
      line-height: 120%;
    }
  }
  .access-airdrop__nav{
    .m-btn{
      height: 2.6875em;
      span{
        font-size: 1em;
      }
    }
  }
}

.main-slider{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  .swiper{
    &-container{
      height: 100%;
    }
    &-slide{
      background-size: cover;
      background-position: center;
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    &__wrap{
      padding: 2em .75em;
    }
    &__item{
      width: 9.5625em;
      height: 2.0625em;
      span{
        font-size: .625em;
      }
      &._type2{
        clip-path: polygon( 0% 0%,100% 0%,100% 25%,91.641% 84.869%,91.641% 84.869%,91.22% 87.601%,90.754% 90.09%,90.248% 92.327%,89.705% 94.299%,89.131% 95.997%,88.527% 97.41%,87.9% 98.527%,87.252% 99.338%,86.587% 99.833%,85.911% 100%,14.089% 100%,14.089% 100%,13.413% 99.833%,12.748% 99.338%,12.1% 98.527%,11.473% 97.41%,10.87% 95.997%,10.295% 94.299%,9.752% 92.327%,9.246% 90.09%,8.78% 87.601%,8.359% 84.869%,0% 25%,0% 0% );
        width: 10.625em;
      }
    }
  }
}
