.leaderboard-top{
  padding: .5em 0;
  &__inner{
    background-color: $primary;
    background-image: url(../img/leaderboard-bg.svg);
    background-size: cover;
    background-position: center;
    padding: 2em 2.75em 3.44em;
    border-radius: .75em;
    color: #fff;
    @include grid100gap(.75em);
  }
}

.leaderboard{
  padding: .75em 0 5em;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 27.875em - 2.31em) 27.875em;
    gap: 2.31em;
  }
  @media screen and (min-width: 577px){
    .tabs{
      display: none;
    }
    .tab-content{
      display: block;
    }
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}

.tabs{
  border-radius: 6.25em;
  border: 1px solid rgba(205, 205, 205, 0.50);
  padding: .375em;
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .375em));
  gap: .75em;
}

.tab{
  height: 2.1875em;
  @include center;
  padding: 0 1em;
  color: #CDCDCD;
  transition: all .3s;
  border: 1px solid transparent;
  font-weight: 500;
  border-radius: 6.25em;
  &:hover{
    cursor: pointer;
    color: $primary;
  }
  &._active{
    border-color: $primary;
    color: $primary;
  }
  span{
    font-size: 1em;
    line-height: 120%;
  }
}

.leaderboard-table{
  &__grid{
    display: grid;
    grid-template-columns: 3.1875em calc(100% - 34.1875em) 7em 21em;
    gap: 1em;
  }
  &__items{
    @include grid100gap(.375em);
  }
}

.leaderboard-head{
  padding-bottom: 1em;
  &__col{
    color: #5C5C5C;
    &:first-child{
      text-align: right;
    }
    &:last-child{
      padding-right: 1.5em;
      text-align: right;
    }
    &:nth-child(3){
      text-align: center;
    }
    span{
      font-size: .75em;
      line-height: 125%;
    }
  }
}

.leaderboard-item{
  min-height: 3.375em;
  align-items: center;
  border-radius: 0.75em;
  border: 1px solid rgba(205, 205, 205, 0.50);
  background: #FCFCFC;
  box-shadow: .125em .125em 0 0 #D7DCF9;
  overflow: hidden;
  &._blue{
    .leaderboard-item__num{
      color: $primary;
    }
    .leaderboard-item__name{
      color: $primary;
      font-weight: 500;
    }
  }
  &__num{
    @include center;
    min-height: 100%;
    background-color: #F7F7F7;
  }
  &__name{
    display: flex;
    align-items: center;
    .avatar{
      width: 1.875em;
      height: 1.875em;
      margin-right: .75em;
    }
  }
  &__invited{
    text-align: center;
  }
  &__points{
    text-align: right;
    padding-right: 1.5em;
  }
}

.recent-joins{
  padding: 1.25em 1.5em;
  border-radius: 0.75em;
  border: 1px solid rgba(205, 205, 205, 0.50);
  background: $white;
  box-shadow: .125em .125em 0 0 #D7DCF9;
  &__top{
    margin-bottom: 1.25em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.join-item{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: .625em 0;
  border-bottom: 1px solid rgba(#CDCDCD,.3);
  &:first-child{
    padding-top: 0;
  }
  &:last-child{
    padding-bottom: 0;
    margin-bottom: 0;
    border-bottom: none;
  }
  &__content{
    display: flex;
    align-items: center;
  }
  .avatar{
    margin-right: .75em;
    width: 1.875em;
    height: 1.875em;
  }
  &__info{
    @include grid100gap(.13em);
  }
  &__time{
    padding-left: .5em;
  }
  &__btn{
    width: 6.25em;
    height: 1.5em;
    padding: 0;
    border-radius: .25em;
    span{
      font-size: .75em;
    }
  }
}

.copy-alert{
  position: fixed;
  background-color: $primary;
  color: #fff;
  right: 1em;
  bottom: 1em;
  z-index: 20;
  opacity: 0;
  pointer-events: none;
  padding: .5em 2em;
  border-radius: .5em;
  transition: all .3s;
  &._active{
    opacity: 1;
    pointer-events: unset;
  }
}



@media screen and (max-width: 1024px){
  .leaderboard{
    &__inner{
      grid-template-columns: calc(100% - 21.6875em - .625em) 21.6875em;
      gap: .625em;
    }
  }

  .leaderboard-table{
    &__grid{
      grid-template-columns: 3.1875em calc(100% - 26.1875em) 7em 13em;
    }
  }

  .leaderboard-head{
    &__col{
      &:last-child{
        padding-right: 0;
      }
    }
  }

  .recent-joins{
    padding: 1.25em 1em;
  }
}


@media screen and (max-width: 576px){
  .leaderboard-top{
    &__inner {
      padding: 1.25em 1em 4.06em;
      background-image: url(../img/leaderboard-bg2.svg);
    }
  }

  .leaderboard{
    &__inner{
      gap: 0;
      grid-template-columns: 100%;
    }
    .tabs{
      margin-bottom: .75em;
    }
  }

  .leaderboard-table{
    &__grid{
      grid-template-columns: calc(100% - 14em) 6em 7em;
      gap: .5em;
    }
  }

  .leaderboard-head{
    padding-bottom: .75em;
    &__col{
      padding: 0 .625em;
      &:nth-child(1){
        display: none;
      }
      &:nth-child(2){
        padding-right: 0;
      }
      &:nth-child(3){
        text-align: center;
      }
      &:last-child{
        padding: 0;
      }
    }
  }

  .leaderboard-item,
  .join-item{
    .avatar{
      width: 1.5em;
      height: 1.5em;
      margin-right: .5em;
    }
  }

  .leaderboard-item{
    min-height: 2.5em;
    padding: .25em 0;
    &__num{
      display: none;
    }
    &__name{
      padding-left: .625em;
    }
    &__invited{
      padding: 0 .625em;
    }
    &__points{
      padding-right: .875em;
    }
  }

  .recent-joins{
    padding: 0;
    border: none;
    box-shadow: none;
    &__top{

    }
  }

  .join-item{

  }
}
