@import 'vars';
html {
    width: 100%;
    &.fixed-body {
        overflow-y: hidden;
    }
}

.wrapper {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
}

.content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    width: 100%;
}

.m-page {
    background-image: url(../img/page-bg.webp);
    background-size: 100% auto;
    position: relative;
    width: calc(100vw - (100vw - 100%));
    overflow-x: hidden;
    font-family: $font1;
    font-weight: 400;
    color: $black;
    font-size: calc(1440px * (16 / 1440));
    @media screen and (max-width: 1440px){
        font-size: calc(100vw * (16 / 1440));
    }
    &.fixed-body,
    &.fixed-body2{
        overflow-y: hidden;
        padding-right: var(--scrollWidth);
        padding-right: 0;
    }
    &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-image: url(../img/lines.svg);
        background-repeat: repeat-y;
        background-size: 100% auto;
        z-index: 2;
        pointer-events: none;
    }
    &.not-lines{
        &:before{
            display: none;
        }
    }
}

.container {
    max-width: 1440px;
    width: 100%;
    margin: 0 auto;
    padding: 0 2em;
}

a {
    &:hover {
        color: $primary;
    }
}


@media screen and (max-width: 1024px){
    .m-page{
        font-size: calc(100vw * (16 / 1024));
        &:before{
            background-image: url(../img/lines2.svg);
        }
    }

    .container{
        padding: 0 1.25em;
    }
}


@media screen and (max-width: 576px){
    .m-page{
        font-size: calc(100vw * (16 / 375));
        &:before{
            background-image: url(../img/lines3.svg);
        }
    }

    .container{
        padding: 0 .75em;
    }
}


