.sections{
  overflow: hidden;
  position: relative;
  &__img{
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
  }
}

.airdrop{
  padding: 5em 0;
  &__top{
    @include grid100gap(1.44em);
    text-align: center;
    max-width: 39em;
    margin: 0 auto;
  }
  &__items{
    display: flex;
    justify-content: space-between;
    margin: 2.625em auto 0;
    max-width: 57.0625em;
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 1.5em;
  }
}

.airdrop-card{
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  &:not(:last-child) .airdrop-card__top{
    &:after{
      content: '';
      position: absolute;
      width: 18.25em;
      height: .1875em;
      left: calc(100% + .1875em);
      top: 3em;
      transform: translateY(-50%);
      background-size: 100% auto;
      background-repeat: no-repeat;
      background-position: center;
    }
  }
  &:nth-child(1) .airdrop-card__top{
    &:after{
      background-image: url(../img/line.svg);
    }
  }
  &:nth-child(2) .airdrop-card__top{
    &:after{
      background-image: url(../img/line2.svg);
    }
  }
  &._active{
    .airdrop-card__top{
      border-color: $primary;
    }
    .airdrop-card__month{
      color: #fff;
      background-color: $primary;
    }
    .h4{
      color: $primary;
    }
  }
  &__top{
    width: 6.25em;
    height: 6.25em;
    border-radius: 50%;
    border: 3px solid rgba(67, 78, 220, 0.30);
    background: rgba(204, 204, 248, 0.30);
    position: relative;
    img{
      @include centerAbsolute;
      width: 3em;
    }
  }
  &__month{
    position: absolute;
    left: 50%;
    bottom: -.625em;
    transform: translateX(-50%);
    padding: 0 .5em;
    border-radius: 0.25em;
    background: #B9BDF1;
    display: flex;
    align-items: center;
    height: 1.4375em;
    color: #fff;
    span{
      font-size: .75em;
    }
  }
  &__text{
    margin-top: 1.125em;
    @include grid100gap(.5em);
  }
  .h4{
    color: #B9BDF1;
  }
}

.airdrop-hint{
  margin: 2.5em auto 0;
  max-width: 57.0625em;
  width: 100%;
  background: $gradient;
  border-radius: 1em;
  border: 1px solid $primary;
  padding: 1em;
  flex-direction: column;
  @include center;
  text-align: center;
  min-height: 5.25em;
  &__text{
    max-width: 41.5em;
    a{
      text-decoration: underline;
      color: $primary;
    }
  }
}

.team{
  padding: 5em 0 7.5em;
}

.team-card{
  padding: 1.5em;
  display: flex;
  align-items: center;
  &:before{
    background: #AE9BF8;
    opacity: .1;
  }
  .avatar{
    margin-right: 1.25em;
  }
  &__content{
    @include grid100gap(.5em);
  }
}

.avatar{
  width: 3.3125em;
  height: 3.3125em;
  flex-shrink: 0;
  border-radius: 50%;
  img{
    width: 100%;
    height: 100%;
    display: block;
    border-radius: inherit;
  }
}

.team-info{
  padding: 1.5em;
  border-radius: 1em;
  border: 1px solid rgba(205, 205, 205, 0.50);
  background-color: $primary;
  color: #fff;
  display: flex;
  align-items: center;
  background-image: url(../img/team-bg.svg);
  background-repeat: no-repeat;
  background-position: left 9.6875em center;
  background-size: 20.75em auto;
  img{
    width: 2.75em;
    margin-right: 1.25em;
  }
}

.why,
.companies{
  &__inner{
    width: 71.5em;
    max-width: 100%;
    margin: 0 auto;
  }
}

.why{
  padding: 6.25em 0;
  &__inner{
    background-color: #fff;
    display: grid;
    grid-template-columns: 31.25em calc(100% - 31.25em);
    padding: 0 2.875em 0 .9375em;
    border-radius: 1em;
    border: $border;
    align-items: center;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    width: calc(100% + 3.25em);
    margin-left: -3.25em;
    padding: 1em 0;
  }
  &__items,
  &__top{
    @include grid100gap(.75em);
  }
  &__desc{
    color: #5C5C5C;
  }
  &__items{
    margin-top: 1.5em;
  }
  &__item{
    padding: 1.13em 1.5em;
    border-radius: 1em;
    border: $border;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      width: 0.4375em;
      height: calc(100% + 2px);
      left: -1px;
      top: -1px;
      background-color: $primary;
      border-radius: 1em 0 0 1em;
    }
    &-text{
      max-width: 34.75em;
    }
  }
}

.companies{
  padding: 6.25em 0;
  &__inner{
    display: grid;
    grid-template-columns: 27.8125em calc(100% - 35.8125em);
    gap: 2.5em 4.875em;
  }
  &__img{
    margin-top: 2.75em;
    height: 2.75em;
    display: flex;
    align-items: center;
    img{
      display: block;
      max-width: 100%;
      max-height: 100%;
    }
    ._view2,
    ._view3{
      display: none;
    }
  }
}


@media screen and (max-width: 1024px){
  .airdrop{
    padding: 6.25em 0;
    &__items{
      margin-top: 2.125em;
    }
    &__nav{
      margin-top: 2.5em;
    }
  }

  .airdrop-hint{
    margin-top: 2em;
  }

  .team{
    padding: 6.25em 0;
  }

  .why{
    &__inner{
      padding: 0 1.5em 0 0;
      grid-template-columns: 27.1875em calc(100% - 27.1875em);
    }
    &__content{
      padding: 3em 0;
      width: calc(100% + 2.5625em);
      margin-left: -2.5625em;
    }
  }

  .companies{
    &__inner{
      grid-template-columns: 21.8125em calc(100% - 21.8125em - 4.875em);
    }
    &__col{
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }
    &__img{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .airdrop{
    padding: 3.125em 0;
    &__top{
      gap: 1em;
    }
    &__items{
      margin-top: 1.44em;
      @include grid100gap(4.69em);
    }
    &__nav{
      margin-top: 1.25em;
    }
  }

  .airdrop-card{
    &:not(:last-child){
      &:after{
        content: '';
        position: absolute;
        top: 100%;
        left: 50%;
        transform: translateX(-50%);
        margin-top: .0625em;
        width: .1875em;
        height: 4.25em;
        background-size: 100%;
        background-position: center;
        background-repeat: no-repeat;
      }
    }
    &:nth-child(1):after{
      background-image: url(../img/line3.svg);
    }
    &:nth-child(2):after{
      background-image: url(../img/line4.svg);
    }
    &__top{
      &:after{
        display: none;
      }
    }
  }

  .airdrop-hint{
    margin-top: 1.875em;
  }

  .why,
  .team{
    padding: 3.125em 0;
  }

  .team-info{
    padding: 1.375em 1.5em;
  }

  .why{
    &__inner{
      padding: 0;
      grid-template-columns: 100%;
    }
    &__content{
      padding: 1.5em 1em .375em;
      width: 100%;
      margin: 0;
      order: -1;
    }
    &__top{
      gap: 1em;
    }
    &__items{
      margin-top: 1em;
      gap: .5em;
    }
    &__item{
      padding: 1em 1.44em;
      border-radius: .5em;
    }
  }

  .companies{
    padding: 3.125em 0;
    &__inner{
      grid-template-columns: 100%;
    }
    &__img{
      margin-top: 1.375em;
      ._view2{
        display: none;
      }
      ._view3{
        display: block;
        max-height: 2.3125em;
      }
    }
  }
}
