.operation{
  &__inner{
    padding: 1.875em 0;
    min-height: calc(100 * var(--vh) - 4.25em);
    width: 26.0625em;
    max-width: 100%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    &._full{
      width: 100%;
    }
  }
}

.action{
  &__tabs{
    display: flex;
    justify-content: center;
  }
  &__tab{
    clip-path: polygon( 0.281% 98.81%,99.719% 98.81%,99.719% 77.381%,93.242% 22.295%,93.242% 22.295%,92.74% 18.562%,92.149% 15.135%,91.478% 12.034%,90.735% 9.28%,89.928% 6.894%,89.064% 4.895%,88.151% 3.305%,87.197% 2.144%,86.21% 1.432%,85.197% 1.19%,14.803% 1.19%,14.803% 1.19%,13.79% 1.432%,12.803% 2.144%,11.849% 3.305%,10.936% 4.895%,10.072% 6.894%,9.265% 9.28%,8.522% 12.034%,7.851% 15.135%,7.261% 18.562%,6.758% 22.295%,0.281% 77.381%,0.281% 98.81% );
    @include lastM(0 .75em 0 0);
    padding: .56em;
    width: 11.0625em;
    height: 2.5625em;
    display: flex;
    justify-content: center;
    background-color: $primaryLight;
    color: $primary;
    font-weight: 600;
    font-family: $font2;
    transition: all .3s;
    cursor: pointer;
    span{
      font-size: .75em;
      line-height: 125%;
    }
    &:hover,
    &._active{
      background-color: $primary;
      color: #fff;
    }
  }
  &__content{
    background-color: $primary;
    margin-top: -.625em;
    position: relative;
    z-index: 2;
    border-radius: 1em;
    overflow: hidden;
  }
  &__top{
    padding: 1em .75em .875em;
    @include grid100gap(.25em);
    color: $white;
  }
  &__bottom{
    padding: .75em .75em 1.5em;
    border-radius: .75em .75em 1em 1em;
    background-color: $white;
    @include grid100gap(.5em);
  }
  &__split{
    display: flex;
    justify-content: center;
    color: #E5E5E5;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      height: 1px;
      width: 100%;
      background-color: #E5E5E5;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
    }
    .text12{
      background-color: #fff;
      position: relative;
      z-index: 2;
      padding: 0 .5em;
    }
  }
  &__hint{
    text-align: center;
  }
  &__btn{
    margin-top: .25em;
    height: 2.8em;
  }
}

.stakes,
.action-stat{
  padding: .75em 1em;
  border-radius: 0.75em;
  border: 1px solid rgba(255, 255, 255, 0.50);
}

.stakes{
  &__items{
    display: flex;
    flex-wrap: wrap;
    width: calc(100% + .75em);
  }
  &__item{
    display: flex;
    align-items: center;
    margin: .3125em .75em 0 0;
    img{
      width: 1em;
      margin-right: .25em;
    }
    span{
      font-size: .75em;
      line-height: 120%;
    }
  }
}

.action-stat{
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  &__item{
    min-width: 7.0625em;
    @include grid100gap(.25em);
  }
  &__change{
    color: #6FFF75;
  }
}

.action-rate__item,
.action-rate{
  display: flex;
  align-items: center;
}

.action-rate{
  &__item{
    font-weight: 600;
    font-family: $font2;
    margin-right: .25em;
    span{
      font-size: .75em;
      line-height: 125%;
    }
    img{
      width: .875em;
      margin-left: .25em;
    }
  }
}

.token{
  padding: .625em 1em;
  border-radius: 0.75em;
  border: 1px solid rgba(205, 205, 205, 0.50);
  display: flex;
  align-items: center;
  color: $gray;
  min-height: 3em;
  img{
    width: 1.75em;
    margin-right: .625em;
  }
  &._shadow{
    box-shadow: 1.6px 1.6px 0  rgba(204, 204, 248, 0.60);
  }
}

.token-field{
  position: relative;
  &._error{
    input{
      border-color: $red;
    }
  }
  input{
    font-size: 1em;
    padding: 0 9em 0 1em;
    border-radius: 0.75em;
    border: 1px solid rgba(205, 205, 205, 0.50);
    background: $white;
    box-shadow: 1.6px 1.6px 0  rgba(204, 204, 248, 0.60);
    height: 3em;
    width: 100%;
    color: $black;
    &:focus{
      border-color: #CDCDCD;
      box-shadow: 1.6px 1.6px 0 #CCCCF8;
    }
    &::placeholder{
      color: $grayLight;
    }
  }
  &__select{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 1em;
    display: flex;
    align-items: center;
    color: $gray;
    &:hover{
      cursor: pointer;
      color: $primary;
    }
  }
  &__icon{
    height: 1.75em;
    width: auto;
    margin-right: .6em;
  }
  &__arrow{
    width: 1em;
    margin-left: .8em;
  }
  &__error{
    color: $red;
  }
}

.token-balance{
  display: flex;
  align-items: center;
  .text12{
    margin-right: .67em;
  }
  img{
    width: .75em;
    margin-right: .5em;
  }
  &__btn{
    @include center;
    padding: 0 .375em;
    border-radius: 5em;
    border: 0.8px solid $grayLight;
    height: 0.9375em;
    color: $grayLight;
    &:hover{
      color: $primary;
      border-color: $primary;
    }
    span{
      font-size: .5em;
      line-height: 120%;
    }
  }
}

.select-token{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: $vh;
  z-index: 20;
  text-align: center;
  padding: 3em 0;
  overflow-y: auto;
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  &._active{
    opacity: 1;
    pointer-events: unset;
  }
  &:after {
    content: '';
    display: inline-block;
    height: 100%;
    vertical-align: middle;
    margin-right: -4px;
  }
  &__bg{
    background: rgba(111, 134, 255, 0.20);
    backdrop-filter: blur(5px);
    height: $vh;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
  }
  &__wrap{
    text-align: left;
    vertical-align: middle;
    display: inline-block;
    position: relative;
    z-index: 3;
    width: 26.0625em;
    max-width: calc(100% - 1.5em);
  }
  &__title{
    width: 12.9375em;
    height: 2em;
    background-color: $primary;
    color: #fff;
    clip-path: polygon( 0% 100%,100% 100%,94.43% 27.7%,94.43% 27.7%,93.998% 22.8%,93.49% 18.302%,92.914% 14.232%,92.275% 10.617%,91.581% 7.485%,90.838% 4.862%,90.052% 2.775%,89.232% 1.251%,88.383% 0.317%,87.513% 0%,12.488% 0%,12.488% 0%,11.617% 0.317%,10.768% 1.251%,9.948% 2.775%,9.163% 4.862%,8.42% 7.485%,7.725% 10.617%,7.086% 14.232%,6.51% 18.302%,6.002% 22.8%,5.57% 27.7%,0% 100% );
    @include center;
    font-family: $font2;
    font-weight: 600;
    margin: 0 auto;
    span{
      font-size: .75em;
      line-height: 125%;
    }
  }
  &__items{
    padding: 1.75em 1em;
    border-radius: 1em;
    border: 1px solid #CDCDCD;
    background: rgba(255, 255, 255, 0.80);
    backdrop-filter: blur(5px);
    @include grid100gap(.5em);
  }
}

.token-option{
  padding: 1em 1.5em;
  border-radius: 0.5em;
  border: 1px solid rgba(205, 205, 205, 0.50);
  background: rgba(252, 252, 252, 0.20);
  box-shadow: 2px 2px 0px 0px #CCCCF8;
  display: flex;
  align-items: center;
  justify-content: space-between;
  transition: all .3s;
  cursor: pointer;
  &:not(._active){
    .token-option__right:after{
      width: 0;
      opacity: 0;
      margin: 0;
    }
  }
  &:hover,
  &._active{
    background: $white;
    border-color: rgba(205, 205, 205, 0.50);
    box-shadow: 2px 2px 0px 0px #CCCCF8;
  }
  &__left,
  &__right{
    display: flex;
    align-items: center;
  }
  &__left{
    img{
      width: auto;
      height: 2em;
      margin-right: .75em;
    }
  }
  &__right{
    &:after{
      content: '';
      flex-shrink: 0;
      width: 0.9375em;
      height: 0.625em;
      background-image: url(../img/check2.svg);
      @include bgImgSize(100%);
      margin-left: .5em;
      transition: all .3s;
    }
  }
}


@media screen and (max-width: 1024px){

}


@media screen and (max-width: 576px){
  .operation__inner{
    min-height: calc(100 * var(--vh) - 3.5em);
  }

  .action{
    &__tab{
      width: 9.5em;
      height: 2.875em;
      @include lastM(0 .56em 0 0);
      padding: .5em;
      clip-path: polygon( 0% 100%,100% 100%,100% 75%,89.884% 17.338%,89.884% 17.338%,89.273% 14.245%,88.574% 11.414%,87.796% 8.86%,86.946% 6.599%,86.032% 4.645%,85.062% 3.012%,84.044% 1.717%,82.985% 0.773%,81.893% 0.196%,80.777% 0%,19.223% 0%,19.223% 0%,18.106% 0.196%,17.015% 0.773%,15.956% 1.717%,14.938% 3.012%,13.968% 4.645%,13.054% 6.599%,12.204% 8.86%,11.426% 11.414%,10.727% 14.245%,10.116% 17.338%,0% 75%,0% 100% );
    }
    &__content{
      margin-top: -1em;
    }
    &__top{
      padding: 1em;
    }
    &__bottom{
      padding: 1em 1em 1.75em;
      border-radius: 1em;
    }
  }

  .stakes{
    &__item{
      margin: .25em .375em 0 0;
      span{
        font-size: .625em;
      }
    }
  }

  .action-stat{
    &__item{
      min-width: unset;
    }
  }

  .select-token{
    &:after{
      display: none;
    }
  }
}
