@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1em;
    height: 3em;
    font-weight: 600;
    border-radius: 0.5em;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1em;
        line-height: 120%;
    }
}

.m-btn-blue{
    background-color: $primary;
    color: $white;
    &:hover{
        background-color: #5B66EB;
        color: $white;
    }
    &._bg{
        background-image: url(../img/btn-bg.svg);
        background-size: cover;
        background-position: center;
    }
    &:disabled,
    &._disabled{
        pointer-events: none;
        background-color: rgba($primary,.5);
    }
}


.m-btn-white{
    color: $primary;
    border: 1px solid #fff;
    background-color: #fff;
    &:hover{
        background-color: $primary;
        color: #fff;
        path{
            fill: #fff;
        }
    }
}

.m-btn-white-outline{
    color: #fff;
    border: 1px solid #fff;
    &:hover{
        background-color: $primary;
        color: #fff;
    }
}

.m-btn-blue-outline{
    color: $primary;
    border: 1px solid $primary;
    &:hover{
        background-color: $primary;
        color: #fff;
    }
}
